import api from "../index";
import { HouseDTO } from "./model.interface";

export const get = async () => await api.get("houses");
export const details = async (id: number) => await api.get(`houses/${id}`);
export const post = async (houseDTO: HouseDTO) =>
  await api.post(`houses`, houseDTO);
export const put = async (id: number, houseDTO: HouseDTO) =>
  await api.put(`houses/${id}`, houseDTO);
