import React, { ReactElement } from "react";
import { withRouter } from "react-router";
import { List, ListItem, ListItemText } from "@material-ui/core";

import { House } from "../../api/houses/model.interface";
import * as HouseAPI from "../../api/houses";
import withPageLayout from "../../hocs/withPageLayout";
import { PageHeaderContext } from "../../context/layout";

const HouseList = (props: any) => {
  const LayoutContext = React.useContext(PageHeaderContext);

  const [houses, setHouses] = React.useState([]);
  React.useEffect(() => {
    props.setShowLoading(true);
    LayoutContext.setTitle("House List");
    getData();
  }, []);

  const getData = async () => {
    const data: any = await HouseAPI.get();
    setHouses(data);
    props.setShowLoading(false);
  };
  return (
    <>
      <List>
        {houses.map((house: House) => {
          return (
            <ListItem button onClick={() => props.history.push("/profile")}>
              <ListItemText primary={house.name} />
            </ListItem>
          );
        })}
      </List>
    </>
  );
};
export default withRouter(withPageLayout(HouseList));
